import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MaterialModule} from '@angular/material';
import 'hammerjs';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { AppoinmentComponent } from './appoinment/appoinment.component';
import { AssignmentComponent } from './assignment/assignment.component';
import { LivetrackingComponent } from './livetracking/livetracking.component';
import { InvoiceComponent } from './invoice/invoice.component';
import { ReportComponent } from './report/report.component';
import { TechnicianSchedulerComponent } from './technician-scheduler/technician-scheduler.component';
import { TechnicianAssignmentComponent } from './technician-assignment/technician-assignment.component';
import { SettingsComponent } from './settings/settings.component';
import { ProfileComponent } from './profile/profile.component';

const appRoutes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'appoinment',      component: AppoinmentComponent },
  { path: 'assignment',      component: AssignmentComponent },
  { path: 'livetracking',      component: LivetrackingComponent },
  { path: 'invoice',      component: InvoiceComponent },
  { path: 'report',      component: ReportComponent },
  { path: 'technicianScheduler',      component: TechnicianSchedulerComponent },
  { path: 'technicianAssignment',      component: TechnicianAssignmentComponent },
  { path: 'settings',      component: SettingsComponent },
  { path: 'profile',      component: ProfileComponent },
  // {
  //   path: 'heroes',
  //   component: HeroListComponent,
  //   data: { title: 'Heroes List' }
  // },
  { path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
  // { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AppoinmentComponent,
    AssignmentComponent,
    LivetrackingComponent,
    InvoiceComponent,
    ReportComponent,
    TechnicianSchedulerComponent,
    TechnicianAssignmentComponent,
    SettingsComponent,
    ProfileComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MaterialModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



